html {
	box-sizing: border-box;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
img {
	max-width: 100%;
}
a {
	text-decoration: none;
}
ul {
	list-style: none;
	padding: 0;
}

@import url("https://fonts.googleapis.com/css?family=Montserrat:regular,italic,500,500italic,700,700italic&display=swap&subset=cyrillic-ext");

body {
	margin: 0;
	font-size: 16px;
	color: $pri-text-col;
	line-height: 1.6;
	font-family: 'Montserrat', sans-serif;
}

.container {
	max-width: 1215px;
	min-height: 100%;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.section__title {
	text-transform: uppercase;
	.title {
		font: $very-high-text;
		@include adaptiv-value('font-size', 28, 26, 1);

	}
	.suptitle {
		font: $middle-text-bold;
		@include adaptiv-value('font-size', 40, 26, 1);
	}
}
._anim-show {
	transform: translate(0px, 120%);
	opacity: 0;
	transition: 0.8s ease 0s;
}

._anim-show._active,
._active ._anim-show {
	opacity: 1;
	transform: translate(0px, 0px);
}

.site__container {
	// overflow: hidden;
}

.btn-reset {
	border: none;
	background: transparent;
	cursor: pointer;
}

.is-outer {
	position: relative !important; // stylRelint-disable-line declaration-no-important
	left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.is-hidden {
	display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
	display: none;
}

.error {
	border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}
